@import url('https://fonts.googleapis.com/css2?family=Kite+One&family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap');

.nav-list {
    display: flex;
    width: 90%;
    list-style: none;
    gap: 8.75rem;
}

.language .img-language {
    width: 40px;
    height: 40px;
}
a{  
    color: var(--main-white-color);
    font-family: "Roboto Condensed";
    font-size: 100%;
    font-style: normal;
    width: 100%;
    line-height: 98.438%;
    text-decoration: none;
}

.language {
    border-radius: 10px;
    background: transparent;
    border: none;
    border-style: none;
    font-family: "Kite One";
    font-style: normal;
    font-weight: 400;
    cursor: pointer;    
}

.nav-name{
    display: flex;
    align-items: center;
    color: var(--main-white-color);
    flex-shrink: 0;
    font-family: "Kite One";
    font-size: 2.5rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

#about, .about{
    width: 140%;
}

